.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  padding: 0;
  margin: 0 30px 0 0;
  grid-area: image;
  position: relative;
  object-fit: cover;
}

@media screen and (min-width: 320px) and (max-width: 1139px) {
  .profile__avatar {
    margin: 0 auto;
  }
}